
  * {
  font-family: "Nunito", sans-serif ;
  color: #202020;
  font-weight: 800;
  }

body {
  margin: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: "Nunito", sans-serif ;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas,
    monospace;
}

.ScrollToTop {
  position: fixed; 
  bottom: 40px;
  align-items: center;
  justify-content: right;
  z-index: 1000;
  cursor: pointer;
  animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.7;
  height: 48px;
  width: 48px;
  border-width: 5px;
  margin: 20px;
}

.ScrollToTop:hover{
  opacity: 1;
}

@media (prefers-reduced-motion: no-preference) {
  .ScrollToTop {
    animation-name: ScrollToTop-spin;
    animation-iteration-count: 1;
    animation-duration: 2s, 4s;
    animation-timing-function: ease;
    transition: transform 1s;
  }
}

@keyframes ScrollToTop-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes ScrollToTop-slidein {
  from {
    margin-left: 100%;
  }
  to {
    margin-left: 0%;
  }
}

@keyframes ScrollToTop-fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
